import React from "react";
import { LazyLoadImage } from 'react-lazy-load-image-component';

import "./styles.scss";

const Img = (props) => {
    if (props.src.includes("imgur.com") && !props.src.endsWith(".jpg")) {
        const highResUrl = props.src + "h.jpg"
        const lowResUrl = props.src + "m.jpg"
        return (
            <div className="LazyLoadImageWrapper">
                <LazyLoadImage {...props} src={highResUrl} delayTime={300} placeholderSrc={lowResUrl} effect="blur" threshold={300} />
            </div>
        );
    } else {
        return (
            <LazyLoadImage {...props} threshold={300} />
        );
    }
};

export default Img; 